<template>
  <view>
    <text>课程详情</text>
    <view style="display: flex; flex-direction: column;">
      <u-image style="align-self: center;" width="300px" height="300px" :src="course.logoUrl" mode="aspectFit"></u-image>
      <text class="u-m-30 u-font-lg">课程名称：{{course.courseName}}</text>
      <text class="u-m-30 u-font-lg">课程编号：{{course.courseCno}}</text>
      <text class="u-m-30 u-font-lg">更新时间：{{course.updateTime}}</text>
      <text class="u-m-30 u-font-lg u-line-2" selectable>{{course.logoUrl}}</text>
    </view>
  </view>
</template>

<script>
    export default {
        data() {
            return {
                course:{}
            }
        },
        onLoad(option){
          console.log("courseId:" + option.courseId)  
          this.loadData(option.courseId);
        },
        methods: {
            loadData(courseId){
                uni.request({
        url: `${this.$config.baseUrl}/edu/course/detail?id=`+courseId,
        method: "GET",
        timeout: 5000,
        success: (res) => {
          console.log("course-detail: " + JSON.stringify(res))
          if(res.data.code != 200) {
            uni.showModal({
              title: "获取课程数据失败",
              content: res.data.message,
              showCancel: false
            })
            return;
          }
          this.course = res.data.data;
        },
        fail: (err) => {
          console.log("login-err: " + JSON.stringify(err))
        }
      })
    }
  }
}
</script>

